import { useEffect, useRef } from "react";
import Peer from "peerjs";
import { connect, useSelector } from "react-redux";
import {
  onCall,
  onConnection,
  processInputStream,
  shareVideoToUser,
} from "./helperFunctions";
import { mapAppDispatchToProps } from "./mapDispatchToProps";
import { mapAppStateToProps } from "./mapStateToProps";
import { AppProps } from "./propsInterface";
import { constraints } from "./constants";
import Home from "./Components/Home";
import GlobalRefsCustom from "./GlobalRefsCustom";

function App({
  setLoading,
  setPeerId,
  setConnectionStatus,
  setRemotePeerIds,
  setSharedScreens,
  sharedScreenIds,
}: AppProps) {
  // these are props that can't set in redux
  const currentUserVideoRef = useRef<any | null>(null);
  const remoteUserVideoRef = useRef<any | null>(null);
  const dontConnectPeer = useRef<any | null>(null);
  const screensAll = useRef<any | null>(null);
  screensAll.current = sharedScreenIds;
  console.log(" callwed main app ..", screensAll.current);

  const params = GlobalRefsCustom();

  useEffect(() => {
    const peer = new Peer();

    peer.on("open", (id) => {
      setPeerId(id);
      setLoading(false);
    });
    peer.on("connection", (conn) => {
      console.log(
        "in on conn......",
        params.remoteConnection,
        "....",
        conn.peer
      );
      if (
        params.remoteConnection.current === null ||
        conn.peer !== params.remoteConnection.current.peer
      ) {
        getScreens(conn);
      }
      console.log(
        "ids peers ...",
        conn.peer,
        "anoyher",
        dontConnectPeer.current
      );
      // if (
      //   dontConnectPeer.current == null ||
      //   dontConnectPeer.current !== conn.peer
      // ) {
      //   console.log("inside");
      //   const remoteConn22 = params.peerInstance.current!.connect(conn.peer);
      //   const abc = "hello";
      //   remoteConn22.send({ abc });
      //   remoteConn22.on("data", (data: any) => {
      //     console.log(" suc on data ....", data);
      //   });
      // }
      // console.log("props..", sharedScreenIds);
      //  conn.send("hello new peer");
      // onConnection(
      //   params.globalScreens,
      //   params.peerInstance,
      //   conn,
      //   setConnectionStatus,
      //   setSharedScreens,
      //   params.remoteConnection
      // );
      setConnectionStatus(true);
      console.log("..............");
      const ids: any = [];
      params.globalScreens.current.forEach((tracksVA: any) => {
        ids.push(tracksVA.uniqueId);
      });
      conn.on("data", (data: any) => {
        console.log("on data......", data);
        if (data.channel.type === "screensfromshareduser") {
          console.log("inside....");
          data.channel.screens.forEach((screen: any) => {
            setSharedScreens(screen);
          });
        } else {
          shareVideoToUser(
            data,
            params.globalScreens,
            params.peerInstance,
            conn
          );
        }
      });
      console.log("setting params");
      params.setRemoteConnection(conn);
    });
    peer.on("call", (call) => {
      onCall(addRemotePeerId, call, remoteUserVideoRef);
    });

    params.setPeerInstance(peer);

    return () => {
      if (params.peerInstance.current) {
        params.peerInstance.current.destroy();
      }
    };
  }, []);

  const playVideo = (channelType: string) => {
    console.log("in play..", params.remoteConnection.current.peer);
    const remoteConn = params.peerInstance.current!.connect(
      params.remoteConnection.current.peer
    );
    params.setRemoteConnection(remoteConn);
    // const data = "hello";
    remoteConn.on("open", () => {
      console.log("sending message");
      const channel = { type: "playstream", screen: channelType };
      remoteConn.send({ channel });
    });
    // params.remoteConnection.current!.send({ channel });
  };

  const getScreens = (conn: any) => {
    console.log(" callwed main app 33333..", screensAll.current);

    console.log("sending screens values...", sharedScreenIds);
    const channel = {
      type: "screensfromshareduser",
      screens: screensAll.current,
    };
    console.log("remote conn ", conn.peer);
    const remoteConn = params.peerInstance.current!.connect(conn.peer);
    params.setRemoteConnection(remoteConn);
    const data = "hello";
    remoteConn.on("open", () => {
      console.log("sending screens");
      remoteConn.send({ channel });
    });
  };
  const connectToPeer = (remotePeerId: string) => {
    const remoteConn = params.peerInstance.current!.connect(remotePeerId);
    dontConnectPeer.current = remotePeerId;
    params.setRemoteConnection(remoteConn);
    // const data = "hello";
    remoteConn.on("open", () => {
      console.log("sending message");
      // remoteConn.send({ data });
    });
  };

  const startScreenShare = (screenName: string) => {
    console.log("send...", sharedScreenIds);
    setSharedScreens(screenName);
    navigator.mediaDevices
      .getDisplayMedia(constraints)
      .then((screenStream) => {
        params.setGlobalScreens(
          processInputStream(screenStream, screenName, currentUserVideoRef)
        );
      })
      .catch((err) => {
        console.error("Error accessing screen sharing:", err);
      });
  };

  const addRemotePeerId = (callerPeer: string) => {
    setRemotePeerIds(callerPeer);
  };

  return (
    <div className="App">
      <Home
        connectToPeer={connectToPeer}
        startScreenShare={startScreenShare}
        playVideo={playVideo}
        getScreens={getScreens}
        currentUserVideoRef={currentUserVideoRef}
        remoteUserVideoRef={remoteUserVideoRef}
        dataConnection={params.remoteConnection}
      ></Home>
    </div>
  );
}

export default connect(mapAppStateToProps, mapAppDispatchToProps)(App);
