import React from "react";
import { FaYoutube, FaAmazon } from "react-icons/fa";
import "../App.css";
import { AdditionalActionsProps } from "../propsInterface";
import { connect } from "react-redux";
import { mapAdditionalActionsStateToProps } from "../mapStateToProps";

const AdditionalActions: React.FC<AdditionalActionsProps> = ({
  startScreenShare,
  playVideo,
  sharedScreenIds,
}) => {
  function loopData() {
    return sharedScreenIds.forEach((screen) => {
      return (
        <button className="playIcons" onClick={() => playVideo(screen)}>
          {screen}
        </button>
      );
    });
  }
  return (
    <div className="actions">
      {sharedScreenIds.map((screen) => (
        <button className="playIcons" onClick={() => playVideo(screen)}>
          {screen}
        </button>
      ))}
    </div>
  );
};

export default connect(mapAdditionalActionsStateToProps)(AdditionalActions);
